<template>
    <main id="blogList">
        <!-- 主体 -->
        <section class="main-left">
            <cl-blog v-for  = "item in $store.state.currentBlogData"
                     :title = "item.title"
                     :url   = "item.url"
                     :key   = "item.url"
                     :time  = "item.time"
                     :tags  = "item.tags"
                     :brief = "item.brief"></cl-blog>
            <footer class="text-center">
                <el-pagination
                        v-if="!isMobileSize"
                        @size-change="changeSize"
                        @current-change="changePage"
                        :current-page.sync="currentPage"
                        :page-sizes="[6, 10, 20]"
                        :page-size="$store.state.pageSize"
                        layout="total, prev, pager, next"
                        :total="$store.state.totalBlogLength">
                </el-pagination>
            </footer>
        </section>
        <section class="main-right">
            <cl-info></cl-info>
        </section>
    </main>
</template>
<script>
export default {
  data: function () {
    return {
      currentPage: 1,
      mobileLoading: false,
      isMobileSize: window.innerWidth <= 768
    };
  },
  methods: {
    // 换一页
    changePage (val) {
      this.currentPage = val;
      this.getData();
    },
    // 改变每页数量
    changeSize (val) {
      this.pageSize = val;
      this.getData();
    },
    // PC端 获取数据
    getData () {
      // 根据 pageSize 和 currentPage 获得当前显示的数据
      const that = this;
      const params = {
        type: 0, // 0 blog , 1 demo
        currentPage: that.currentPage
      };
      that.$store.commit('pageChange', params);
    },
    // 手机端 获取数据
    getDataInMobile () {
      const that = this;
      that.currentPage++;
      const params = {
        type: 0,
        currentPage: that.currentPage
      };
      that.$store.commit('loadNextPage', params);
      that.mobileLoading = false;
    },
    // 滚动到底部
    scrollToFooter () {
      const that = this;
      window.addEventListener('scroll', () => {
        const $blog = document.getElementById('blogList');
        if (!$blog || !that.isMobileSize) return false;
        const blogHei = $blog.clientHeight + $blog.offsetTop;
        let totalHei = (document.body.scrollTop || document.documentElement.scrollTop) + window.screen.height;
        if (blogHei < totalHei) {
          that.mobileLoading = true;
          that.getDataInMobile();
        }
      });
    }
  },
  mounted () {
    if (location.hash === '#/blog') document.title = '个人博客 by 龙 - blogList';
    this.scrollToFooter();
  }
};
</script>



